import React from "react";

import ServiceSection from "../components/CMSComponents/ServiceSection";
import '../styles/ServicePage.css'


export default function ServicesPage(props) {
    const servicePageData = React.useMemo(() => {
        return props.data
    }, [props.data])
    return (
        <>
            <section className="main-section">
                <div className="main-section-title section-title-text title">  </div>
                <div className="main-section-text">
                           
                      ,    
                         .
                            ,    ,
                     ,   ,   ,
                        .     
                             -.
                </div>
            </section>
            {/*       */}
            {servicePageData && servicePageData.map((item, index) => (
                <ServiceSection key={item.slug.current} number={index + 1} data={item} />
            ))}
        </>
    )

}